import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

export function renderGallery(images, galleryElement) {
  galleryElement.innerHTML = images.map(img => `
    <a href="${img.largeImageURL}">
      <img src="${img.webformatURL}" alt="${img.tags}">
      <div>üëç ${img.likes} üëÄ ${img.views} üí¨ ${img.comments} ‚¨áÔ∏è ${img.downloads}</div>
    </a>
  `).join("");
  const lightbox = new SimpleLightbox("a", { /* –æ–ø—Ü—ñ—ó */ });
  lightbox.refresh();
}
